"use client";
import { useEffect, useMemo, useState } from "react";
import { useAppStore } from "@/store/useAppStore";

type LLM = {
  id: string;
  model_name: string;
  nickname?: string | null;
};

export default function DashboardClient({
  email,
  initialLlms,
}: {
  email: string;
  initialLlms: LLM[];
}) {
  const { llms, setLLMs, responses, setResponse } = useAppStore();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [saving, setSaving] = useState(false);
  const [form, setForm] = useState({ model_name: "", api_key: "", nickname: "" });
  const [error, setError] = useState<string | null>(null);
  const [prompt, setPrompt] = useState("");
  const [sending, setSending] = useState(false);

  // initialize from server
  useEffect(() => {
    if (initialLlms?.length) setLLMs(initialLlms as any);
  }, [initialLlms, setLLMs]);

  async function refreshLLMs() {
    const res = await fetch("/api/llms");
    if (res.ok) {
      const json = await res.json();
      setLLMs(json.llms);
    }
  }

  async function addLLM(e: React.FormEvent) {
    e.preventDefault();
    setSaving(true);
    setError(null);
    const res = await fetch("/api/llms", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(form),
    });
    setSaving(false);
    if (!res.ok) {
      const t = await res.text();
      setError(t);
      return;
    }
    setForm({ model_name: "", api_key: "", nickname: "" });
    setIsModalOpen(false);
    refreshLLMs();
  }

  async function removeLLM(id: string) {
    await fetch(`/api/llms/${id}`, { method: "DELETE" });
    refreshLLMs();
  }

  const orderedResponses = useMemo(() => {
    return (llms || []).map((l) => responses[l.id]).filter(Boolean);
  }, [llms, responses]);

  async function sendPrompt() {
    if (!prompt.trim()) return;
    setSending(true);
    // set all to loading
    for (const l of llms) {
      setResponse(l.id, { id: l.id, model_name: l.model_name, nickname: l.nickname, status: "loading", content: undefined, error: undefined });
    }
    try {
      const res = await fetch("/api/compare", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ prompt }),
      });
      if (!res.ok) throw new Error(await res.text());
      const json = await res.json();
      for (const r of json.results as any[]) {
        if (r.status === "success") {
          setResponse(r.id, { id: r.id, model_name: r.model_name, nickname: r.nickname, status: "success", content: r.content });
        } else {
          setResponse(r.id, { id: r.id, status: "error", error: r.error });
        }
      }
    } catch (e: any) {
      for (const l of llms) setResponse(l.id, { id: l.id, status: "error", error: String(e.message || e) });
    } finally {
      setSending(false);
    }
  }

  return (
    <div className="min-h-dvh grid grid-cols-1 md:grid-cols-[280px_1fr]">
      <aside className="border-r p-4">
        <div className="mb-4 text-sm text-gray-600">{email}</div>
        <div className="space-y-2">
          <div className="text-xs uppercase text-gray-500">Saved LLMs</div>
          <ul className="space-y-1">
            {llms?.map((l) => (
              <li key={l.id} className="flex items-center justify-between gap-2 text-sm">
                <span className="truncate" title={l.model_name}>
                  {l.nickname || l.model_name}
                </span>
                <button
                  onClick={() => removeLLM(l.id)}
                  className="rounded px-2 py-0.5 text-xs text-red-600 hover:bg-red-50"
                >
                  Delete
                </button>
              </li>
            ))}
          </ul>
          <button
            onClick={() => setIsModalOpen(true)}
            className="mt-2 inline-flex w-full items-center justify-center rounded-md border px-3 py-1.5 text-sm hover:bg-gray-50"
          >
            + Add LLM
          </button>
        </div>
      </aside>
      <main className="p-4">
        <div className="mx-auto max-w-4xl">
          <div className="space-y-3">
            <textarea
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
              placeholder="Enter a prompt to compare across all models..."
              className="min-h-32 w-full resize-y rounded-lg border p-3 shadow-sm outline-none focus:ring"
            />
            <button
              onClick={sendPrompt}
              disabled={sending || !llms?.length}
              className="rounded-lg bg-black px-4 py-2 text-white disabled:opacity-60"
            >
              {sending ? "Sending..." : "Send to all"}
            </button>
          </div>
          <div className="mt-6 grid gap-4 md:grid-cols-2">
            {orderedResponses.length ? (
              orderedResponses.map((r) => (
                <div key={r.id} className="rounded-lg border p-3">
                  <div className="mb-1 flex items-center justify-between text-sm font-medium">
                    <span>{r.nickname || r.model_name || "Model"}</span>
                    <span
                      className={`rounded px-2 py-0.5 text-xs ${
                        r.status === "success"
                          ? "bg-green-100 text-green-700"
                          : r.status === "error"
                          ? "bg-red-100 text-red-700"
                          : r.status === "loading"
                          ? "bg-blue-100 text-blue-700"
                          : "bg-gray-100 text-gray-700"
                      }`}
                    >
                      {r.status === "success" ? "Success" : r.status === "error" ? "Error" : r.status === "loading" ? "Loadingâ€¦" : "Idle"}
                    </span>
                  </div>
                  <div className="prose max-h-80 overflow-auto whitespace-pre-wrap text-sm text-gray-800">
                    {r.status === "error" ? r.error : r.content || ""}
                  </div>
                  {r.content && (
                    <div className="mt-2">
                      <button
                        onClick={() => navigator.clipboard.writeText(r.content!)}
                        className="rounded border px-2 py-1 text-xs hover:bg-gray-50"
                      >
                        Copy
                      </button>
                    </div>
                  )}
                </div>
              ))
            ) : (
              <div className="rounded-lg border p-3 text-sm text-gray-600">Run a prompt to see responses.</div>
            )}
          </div>
        </div>
      </main>

      {isModalOpen && (
        <div className="fixed inset-0 z-50 grid place-items-center bg-black/30 p-4" onClick={() => setIsModalOpen(false)}>
          <div className="w-full max-w-md rounded-xl bg-white p-5 shadow-lg" onClick={(e) => e.stopPropagation()}>
            <div className="mb-3 text-lg font-semibold">Add LLM</div>
            <form onSubmit={addLLM} className="space-y-3">
              <input
                className="w-full rounded-lg border px-3 py-2 outline-none focus:ring"
                placeholder="Model name (e.g., openai/gpt-4o)"
                value={form.model_name}
                onChange={(e) => setForm((f) => ({ ...f, model_name: e.target.value }))}
                required
              />
              <input
                className="w-full rounded-lg border px-3 py-2 outline-none focus:ring"
                placeholder="API key"
                value={form.api_key}
                onChange={(e) => setForm((f) => ({ ...f, api_key: e.target.value }))}
                required
              />
              <input
                className="w-full rounded-lg border px-3 py-2 outline-none focus:ring"
                placeholder="Nickname (optional)"
                value={form.nickname}
                onChange={(e) => setForm((f) => ({ ...f, nickname: e.target.value }))}
              />
              {error && <p className="text-sm text-red-600">{error}</p>}
              <div className="flex items-center justify-end gap-2 pt-1">
                <button type="button" onClick={() => setIsModalOpen(false)} className="rounded px-3 py-1.5 text-sm hover:bg-gray-50">
                  Cancel
                </button>
                <button type="submit" disabled={saving} className="rounded bg-black px-3 py-1.5 text-sm text-white disabled:opacity-60">
                  {saving ? "Savingâ€¦" : "Save"}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
